import { ethers,BigNumber } from "ethers";
import heroAbi from "@/contracts/hero.json";
import goldAbi from "@/contracts/gold.json";
import eventBus from '@/libraries/eventBus.js';

const state = {
  heroSigner:null,
  heroContract:null,
  heroProvider:null,
  heroAbi: heroAbi,
  heroAddress: "0x8daD32976beD22507D79670c096319f342138f0e",

  goldSigner:null,
  goldContract:null,
  goldProvider:null,
  goldAbi: goldAbi,
  goldAllowance:0,
  goldAddress: "0x8c4348a925bab02Fe977887cE335B3409e7C860f",

  myHeroListing:false,
  myHeroes:[],
  myHeroIds:[],
};

const getters = {
  getHeroAbi(state) {
    return state.heroAbi;
  },
  getHeroAddress(state) {
    return state.heroAddress;
  },
  getGoldAbi(state) {
    return state.goldAbi;
  },
  getGoldAddress(state) {
    return state.goldAddress;
  }
};

const actions = {
  // 初始化合约
  async initHero({commit, rootState }){

    if(!rootState.wallet.providerWeb3Modal){
      return;
    }

    // 英雄合约
    let heroProvider = new ethers.providers.Web3Provider(rootState.wallet.providerWeb3Modal);
    let heroSigner =  heroProvider.getSigner(); 
    let heroContract = new ethers.Contract(state.heroAddress, state.heroAbi, heroSigner);
    heroContract.on("OnElementChanged", async (id, point, element) => {
      let owner = await state.heroContract.ownerOf(BigNumber.from(id).toNumber());
      if(owner.toLowerCase()==rootState.wallet.account.toLowerCase()){
        actions.completed({ commit });
      }
    });
    heroContract.on("OnAttributeChanged",async (id, point, attribute) => {
      let owner = await state.heroContract.ownerOf(BigNumber.from(id).toNumber());
      if(owner.toLowerCase()==rootState.wallet.account.toLowerCase()){
        actions.completed({ commit });
      }
    });


    heroContract.on("OnHeroSell",async (id, price) => {
      let owner = await state.heroContract.ownerOf(BigNumber.from(id).toNumber());
      if(owner.toLowerCase()==rootState.wallet.account.toLowerCase()){
        actions.completed({ commit });
      }
    });
    heroContract.on("OnHeroPledge",async (id,owner, amount) => {
      if(owner.toLowerCase()==rootState.wallet.account.toLowerCase()){
        actions.completed({ commit });
      }
    });
    heroContract.on("OnHeroOffer",async (id, buyer,price) => {
      if(buyer.toLowerCase()==rootState.wallet.account.toLowerCase()){
        actions.completed({ commit });
      }
    });
    heroContract.on("OnHeroBought",async (id, seller,buyer,way,price) => {
      if(buyer.toLowerCase()==rootState.wallet.account.toLowerCase() || seller.toLowerCase()==rootState.wallet.account.toLowerCase()){
        actions.completed({ commit });
      }
    });

    heroContract.on("OnHeroDestroyed",async (id,owner) => {
      if(owner.toLowerCase()==rootState.wallet.account.toLowerCase()){
        actions.completed({ commit });
      }
    });
    heroContract.on("OnNameChanged",async (id, name) => {
      let owner = await state.heroContract.ownerOf(BigNumber.from(id).toNumber());
      if(owner.toLowerCase()==rootState.wallet.account.toLowerCase()){
        actions.completed({ commit });
      }
    });
    commit("setHeroSigner", heroSigner);
    commit("setHeroContract", heroContract);
    commit("setHeroProvider", heroProvider);

    // 金币合约
    let goldProvider = new ethers.providers.Web3Provider(rootState.wallet.providerWeb3Modal);
    let goldSigner =  goldProvider.getSigner(); 
    let goldContract = new ethers.Contract(state.goldAddress, state.goldAbi, goldSigner);
    goldContract.on("Approval",async (owner,spender,value) => {
      if(spender.toLowerCase() == state.heroAddress.toLowerCase() && owner.toLowerCase()==rootState.wallet.account.toLowerCase()){
        eventBus.trigger("onConfirmed");
        //eventBus.trigger("onApproved",{owner:owner,spender:spender,value:value});
      }
    });

    //   event Approval(address indexed owner, address indexed spender, uint256 value);
    commit("setGoldSigner", goldSigner);
    commit("setGoldContract", goldContract);
    commit("setGoldProvider", goldProvider);
  },

  // 保存英雄元素属性
  async saveHeroElement({commit, rootState },{ heroId,fire,ice,earth,poison,electric,metal }) {
    try{
      let tx = await state.heroContract.addElement(heroId,fire,ice,earth,poison,electric,metal,{ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
  },

  // 重置英雄元素属性
  async resetHeroElement({commit, rootState },{ heroId,fee }) {
    try{
      let tx = await state.heroContract.resetElement(heroId,{ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
  },

  // 保存英雄基础属性
  async saveHeroAttribute({commit, rootState },{ heroId,strength,intellect,spirit,stamina,agility,luck }) {
    try{
      let tx = await state.heroContract.addAttribute(heroId,strength,intellect,spirit,stamina,agility,luck,{ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
  },
  
  // 重置英雄基础属性
  async resetHeroAttribute({commit, rootState },{ heroId,fee }) {
    try{
      let tx = await state.heroContract.resetAttribute(heroId,{ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
  },

  // 质押英雄
  async pledgeHero({commit, rootState },{ heroId }){
    try{
      let tx = await state.heroContract.pledgeHero(heroId,{ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
  },
  // 赎回英雄
  async redeemHero({commit, rootState },{ heroId,amount }){
    try{
      let tx = await state.heroContract.redeemHero(heroId,{ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
  },

  // 出售英雄
  async sellHero({commit, rootState },{ heroId,price }){
    try{
      let tx = await state.heroContract.sellHero(heroId,ethers.utils.parseUnits(price.toString(), 'ether'),{ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
  },
  // 出售英雄
  async buyHero({commit, rootState },{ heroId,price }){
    try{
      let tx = await state.heroContract.buyHero(heroId,{ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
  },
  // 撤回英雄
  async revokeSellHero({commit, rootState },{ heroId }){
    try{
      let tx = await state.heroContract.revokeHero(heroId,{ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
  },


  // 给英雄出价
  async offerHero({commit, rootState },{ heroId,price }){
    try{
      let tx = await state.heroContract.offerHero(heroId,ethers.utils.parseUnits(price.toString(), 'ether'),{ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
  },
  // 撤回英雄出价
  async cancelHeroOffer({commit, rootState },{ heroId }){
    try{
      let tx = await state.heroContract.cancelHero(heroId,{ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
  },
  // 接受英雄出价
  async acceptHeroOffer({commit, rootState },{ buyer,heroId,price }){
    try{
      let tx = await state.heroContract.acceptOffer(buyer,heroId,ethers.utils.parseUnits(price.toString(), 'ether'),{ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
  },

  // 修改英雄名称
  async changeHeroName({commit, rootState },{ heroId,name,price }){
    try{
      let tx = await state.heroContract.changeName(heroId,name,{ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
  },
  // 销毁英雄
  async destroyHero({commit, rootState },{ heroId }){
    try{
      let tx = await state.heroContract.destroyHero(heroId,{ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
  },

  // 获取许可金额
  async getGoldAllowance({commit, rootState }){
    let amount = 0;
    try{
      let allowance = await state.goldContract.allowance(rootState.wallet.account,state.heroAddress);
      amount  = parseFloat(ethers.utils.formatEther(allowance));
      commit("setGoldAllowance", amount);
      return amount;
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
    return amount;
  },
  // 批准代币数量
  async approveGoldAmount({commit, rootState },{ amount }){
    try{
      let tx = await state.goldContract.approve(state.heroAddress, ethers.utils.parseUnits(amount.toString(), 'ether') ,{ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
  },

  // 执行完成
  async completed({commit, rootState }){
    setTimeout(function(){
      eventBus.trigger("onConfirmed");
      eventBus.trigger("onHeroReload");
    },6000);
  },

    // 获取英雄信息
  async getHeroInfo({commit, rootState },{ heroId }){
    let id = BigNumber.from(heroId).toNumber();
    let hero = await state.heroContract.getHeroInfo(id);
    return hero;
  },

  // 获取英雄列表
  async getMyHeroes({commit,rootState}){
    commit("setMyHeroes", []);
    commit("setLoading", true);
    try{
      let balance = await state.heroContract.balanceOf(rootState.wallet.account);
      const multicallContract = new ethers.Contract(rootState.wallet.isMainnet==true?rootState.wallet.mainnetMulticallAddress:rootState.wallet.testnetMulticallAddress, rootState.wallet.multicallAbi, state.heroSigner);
      const multicallArgs = [];
      for(let i=0;i<balance;i++)
      {
        const approveInterface = new ethers.utils.Interface(["function tokenOfOwnerByIndex(address owner, uint256 index)"]);
        const approveData = approveInterface.encodeFunctionData("tokenOfOwnerByIndex", [
          rootState.wallet.account,
          i,
        ]);
        multicallArgs.push({
          target: state.heroAddress,
          callData: approveData,
        });
      }

      let ids = [];
      let data = await multicallContract.callStatic.aggregate(multicallArgs);
      for(let j=0;j<data.returnData.length;j++)
      {
        let heroId = BigNumber.from(data.returnData[j]).toNumber();
        ids.push(heroId);
      }
      commit("setMyHeroIds",ids);
      
      for(let k=ids.length-1;k>=0;k--)
      {
        let hero = await state.heroContract.getHeroInfo(ids[k]);
        let item =  Object.assign( { status:-1, queue:0 },hero.hero);
        commit("addMyHeroes", { heroId: ids[k], hero: item});
      }
    }catch(e){
      commit("setMyHeroes", []);
      eventBus.trigger("onExecuteError",e);
    }
    commit("setLoading", false);
  },
};

const mutations = {
  setLoading(state,listing){
    state.myHeroListing = listing;
  },
  setMyHeroes(state,heroes){
    state.myHeroes = heroes;
  },
  setMyHeroIds(state,ids){
    state.myHeroIds = ids;
  },
  addMyHeroes(state,hero){
    state.myHeroes.push(hero);
  },
  setMyHero(state,item){
    for(let i=0;i<state.myHeroes.length;i++)
    {
      if(state.myHeroes[i].heroId == item.id){
        state.myHeroes[i].hero.queue = item.queue;
        state.myHeroes[i].hero.status = item.status;
        return;
      }
    }
  },
  setHeroSigner(state, signer){
    state.heroSigner = signer;
  },
  setHeroProvider(state, provider){
    state.heroProvider = provider;
  },
  setHeroContract(state, contract){
    state.heroContract = contract;
  },
  setGoldSigner(state, signer){
    state.goldSigner = signer;
  },
  setGoldProvider(state, provider){
    state.goldProvider = provider;
  },
  setGoldContract(state, contract){
    state.goldContract = contract;
  },
  setGoldAllowance(state, allowance){
    state.goldAllowance = allowance;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
