export function toast(icon,content)
{
    Toast.fire({
        icon: icon,
        // title: content,
        html:'<div class="text-left text-md m-0 p-0 mt-1rem text-gray-300">'+content+'</div>',
    })
}

export function confirm(icon,content,yesCallback,noCallback)
{
    swalWithBootstrapButtons.fire({
    // title: title,
    // text: content,
    html:'<div class="text-center text-md text-gray-300 mt-1rem">'+content+'</div>',
    icon: icon,
    showCancelButton: true,
    confirmButtonText: getCurrentLang()=="en-US"?"Yes":"确认",
    cancelButtonText: getCurrentLang()=="en-US"?"No":"取消",
    reverseButtons: true,
    }).then((result) => {
    if (result.isConfirmed) {
        yesCallback();
    } else if (result.dismiss === Swal.DismissReason.cancel) {
        noCallback();
    }
    });
}


