export async function verifyMysteryDialog(account,blockNumber,hashcode,boxId,language)
{

    function isAddress(address) {
        try {
            ethers.utils.getAddress(address);
        } catch (e) { return false; }
        return true;
    }

    function getBoxes(address,hash,id,lang){

            let number = ethers.utils.keccak256(ethers.utils.solidityPack(['string','address','bytes32'], [id.toString(),address,hash]));
            let random = BigNumber.from(number).mod(BigNumber.from(1000));
            let rarity=lang.mystery.table.rarity.standard;
            let rarityColor = 'bg-gray-700';
            if(random < 2){
                rarityColor = 'bg-red-700';
                rarity = lang.mystery.table.rarity.legendary;// 传说
            }else if(random < 7){
                rarityColor = 'bg-orange-700';
                rarity = lang.mystery.table.rarity.miracle;// 奇迹
            }else if(random < 71){
                rarityColor = 'bg-purple-700';
                rarity = lang.mystery.table.rarity.epic;// 史诗
            }else if(random < 170){
                rarityColor = 'bg-blue-700';
                rarity = lang.mystery.table.rarity.rary;// 精良
            }else if(random < 330){
                rarityColor = 'bg-green-700';
                rarity = lang.mystery.table.rarity.execllent;// 优秀
            }else{
                rarityColor = 'bg-gray-700';
                rarity = lang.mystery.table.rarity.standard;// 普通
            }
        
            return '<div class="flex border-t border-gray-purple-line py-2"><span class="text-sm">'+language.mystery.dialog.verify.box+' #'+id+'</span><span class="ml-auto '+rarityColor+' rounded px-0.5rem py-0.2rem text-xs text-white uppercase">'+rarity+'</span></div>';
    }

    const boxes = getBoxes(account,hashcode,boxId,language);
    const { value: formValues } = await Swal.fire({
        background:"#24242e",
        // icon: "info",
        // title: "Get Free Airdrop",
        // timer: 3000,
        confirmButtonText: language.mystery.dialog.verify.close,
        buttonsStyling: false,
        customClass: {
            confirmButton: 'cursor-pointer w-12em mx-2 my-5 bg-indigo-700 text-white hover:bg-indigo-500 inline-block text-sm text-center appearance-none rounded-full px-3 py-4 leading-tight focus:outline-none',
        },
        html:
        '<div class="text-gray-500 text-center mt-1rem text-sm ">'+language.mystery.dialog.verify.rarityFormula+'</div><input id="mystery-verify-formula" spellcheck="false" readonly="true" value="Rarity = keccak256( #Number, Address, Blockhash ) % 1000" class="text-center w-full bg-gray-purple-input mt-1rem h-2.5rem rounded-md appearance-none p-0.75rem text-sm px-3  text-primary outline-none " >' +
        '<div class="text-gray-500 text-center mt-1rem text-sm ">'+language.mystery.dialog.verify.blockHash+' ( '+blockNumber+' )</div><input id="mystery-verify-hashcode" maxlength="66" autocomplete="off" value="'+hashcode+'" spellcheck="false" class="w-full bg-gray-purple-input mt-1rem h-2.5rem rounded-md appearance-none p-0.75rem text-sm px-3  text-gray-400 outline-none " >'+
        '<div class="text-gray-500 text-center mt-1rem text-sm ">'+language.mystery.dialog.verify.walletAddress+'</div><input id="mystery-verify-address" spellcheck="false" maxlength="42" autocomplete="off" value="'+account+'" class="w-full text-center bg-gray-purple-input mt-1rem h-2.5rem rounded-md appearance-none p-0.75rem text-sm px-3  text-gray-400 outline-none " >' +
        '<div class="text-gray-500 mt-2rem" id="mystery-verify-boxes">'+boxes+'</div>'+
        '<div class="text-left text-xs text-gray-500 mt-1rem">'+language.mystery.dialog.verify.description+'</div>',
        focusConfirm: false,
        didOpen: (toast) => {

            let mysteryBox = document.getElementById('mystery-verify-boxes');
            let hashcodeElement = document.getElementById('mystery-verify-hashcode');
            let addressElement = document.getElementById('mystery-verify-address');

            hashcodeElement.addEventListener('input', function() {
                if(hashcodeElement.value.length==66 && hashcodeElement.value.substring(0,2)=="0x"){
                    mysteryBox.innerHTML = getBoxes(addressElement.value,hashcodeElement.value,boxId,language);
                }else{
                    mysteryBox.innerHTML = language.mystery.dialog.verify.invalidBlockHash;
                }
            });
            addressElement.addEventListener('input', function() {
                if(isAddress(addressElement.value)){
                    mysteryBox.innerHTML = getBoxes(addressElement.value,hashcodeElement.value,boxId,language);
                }else{
                    mysteryBox.innerHTML = language.mystery.dialog.verify.invalidAddress;
                }
                
            });
          }
    });

    return formValues;
}


    


