<script setup>

  import { onMounted, onUnmounted,reactive, ref, toRefs,defineProps } from 'vue';
  import { useLocale,useLocaler } from 'vue-localer';


  const props = defineProps(['language']);

  const timeline = reactive({
    listen: ()=> {
      window.addEventListener('resize', timeline.onResize)
    },
    remove: ()=> {
      window.removeEventListener('resize', timeline.onResize)
    },
    onResize : (event) => {
      if(window.innerWidth<640)
      {
        timeline.isMobileScreen = true;
      }else{
        timeline.isMobileScreen = false;
      }
    },
    isMobileScreen:window.innerWidth<640,
    getTimeLine: ()=>{

      let list = [
    {
      datetime: props.language.phase1,
      events: props.language.phase1Events
    },
    {
      datetime: props.language.phase2,
      events: props.language.phase2Events
    },
    {
      datetime: props.language.phase3,
      events: props.language.phase3Events
    },
    {
      datetime: props.language.phase4,
      events: props.language.phase4Events
    },
    {
      datetime: props.language.phase5,
      events: props.language.phase5Events
    },
    {
      datetime: props.language.phase6,
      events: props.language.phase6Events
    },
    {
      datetime: props.language.phase7,
      events: props.language.phase7Events
    },
    {
      datetime: props.language.phase8,
      events: props.language.phase8Events
    }
      ];

      return list;
    }
  });

  onMounted(() => {
    timeline.listen();
  });

  onUnmounted(()=>{
    timeline.remove();
  });

</script>


<template>
<div class="timeline">
    <div
        v-for="(item, i) in timeline.getTimeLine()"
        v-bind:key="i"
        :class='timeline.isMobileScreen==false && (i % 2) ==0 ? "timeline-item timeline-left text-right  wow animate__bounceInUp" : "timeline-item timeline-right  wow animate__bounceInUp"' >
        <div>
        <div v-html="item.datetime" class="text-yellow-light text-2xl"></div>
                  
        <div :dir='timeline.isMobileScreen==false && (i % 2) ==0 ? "rtl" : "ltr"' class="text-md text-gray-400">
          <ul v-for="(event,j) in item.events" v-bind:key="j" class='list-disc'>
            <li class='my-0.5rem'>{{event}}</li>
          </ul>
        </div>

        </div>
    </div>
</div>
</template>



<style scoped>

.timeline {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
}

.timeline::after {
  content: "";
  position: absolute;
  width: 3px;
  background-color: #b30761;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -2px;
}

.timeline-item {
  padding: 10px 40px;
  position: relative;
  background-color: inherit;
  width: 50%;
}

.timeline-item::after {
    content: "";
    position: absolute;
    width: 1rem;
    height: 1rem;
    right: -0.5rem;
    background-color: #25192b;
    border: 0.2rem solid #b30761;
    top: 1.5rem;
    border-radius: 50%;
    z-index: 1;
}

.timeline-left {
  left: 0;
}

.timeline-right {
  left: 50%;
}

.timeline-right::after {
  left:  -0.5rem;
}

.timeline-content {
  padding: 20px 30px;
  background-color: #16373a;
  position: relative;
}

@media screen and (max-width: 640px) {
  .timeline::after {
    left: 31px;
  }

  .timeline-item {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }

  .timeline-item::before {
    left: 60px;
    border: medium solid #16373a;
    border-width: 10px 10px 10px 0;
    border-color: transparent #16373a transparent transparent;
  }

  .timeline-left::after,
  .timeline-right::after {
    left: 21px;
  }

  .timeline-right {
    left: 0%;
  }
}
</style>