import { createWebHistory, createRouter } from 'vue-router';
import Home from '@/modules/home/index.vue';

export const history = createWebHistory();

export const router = createRouter({
  history,
  routes: [
    {
      path: '/',
      name: 'home',
      meta:{title: ''},
      component: Home,
    },
    {
      path: '/hero',
      name: 'hero',
      meta:{title: ''},
      component: () => import('@/modules/hero/index.vue'),
    },
    {
      path: '/hero/profile/:id',
      name: 'profile',
      // props: route => ({ id: route.query.id }),
      meta:{title: ''},
      component: () => import('@/modules/hero/profile.vue'),
    },
    {
      path: '/mystery/:id?',
      name: 'mystery',
      meta:{title: ''},
      component: () => import('@/modules/mystery/index.vue'),
    },
    {
      path: '/referral',
      name: 'referral',
      meta:{title: ''},
      component: () => import('@/modules/referral/index.vue'),
    },
    {
      path: '/staking',
      name: 'staking',
      meta:{title: ''},
      component: () => import('@/modules/staking/index.vue'),
    },
    {
      path: '/playing',
      name: 'playing',
      meta:{title: ''},
      component: () => import('@/modules/playing/index.vue'),
    },
    // {
    //   path: '/:catchAll(.*)',
    //   name: 'notFound',
    //   component: () => import('~/modules/error-handling/NotFound.vue'),
    // },
  ],
});


router.beforeEach((to, from, next) => {
     document.documentElement.scrollTop = 0;
    next();
 });