<script setup>
  import { onMounted, onUnmounted,reactive, ref,defineProps, toRefs ,defineEmits} from 'vue';

  const emit = defineEmits(['canplay','playing','pause'])
  const props = defineProps(['name','src','poster','maxnone']);
  const player = ref();
  const movie = reactive({
    isCreate:true,
    isDisplay:false,
    extension: props.src.split('.').pop().toLowerCase(),
    start: ()=> {
      movie.onResize();
      window.addEventListener('resize', movie.onResize)
    },
    stop: ()=> {
      window.removeEventListener('resize', movie.onResize)
    },
    canplay: ()=>{
      emit("canplay");
      movie.isDisplay = true;
      if(window.innerWidth>640){
        if(player.value){
           player.value.play();
        }
      }
    },
    playing: ()=>{
      emit("playing");
      movie.isDisplay = true;
    },
    pause: ()=>{
      emit("pause");
    },
    onResize : (event) => {
      if(window.innerWidth<640)
      {
        movie.isCreate=false;
      }else{
        movie.isCreate=true;
        if(player.value){
           player.value.play();
        }
       
      }
    },
  });

  onMounted(() => {
    movie.start();
  });

  onUnmounted(()=>{
    movie.stop();
  });

</script>

<template>
  <video 
    ref="player" v-if="movie.isCreate" v-show="movie.isDisplay" @canplay="movie.canplay" @playing="movie.playing"
    :class="'z-0 w-auto min-w-full min-h-full '+ props.maxnone && props.maxnone == 'true'?'':'max-w-none'"
    muted
    :poster="props.poster"
    autoplay="autoplay"
    loop="loop"
    x5-playsinline="true"
    playsinline="true"
    webkit-playsinline="true"
    x-webkit-airplay="allow"
    preload="auto"
    x5-video-player-type="h5"
    x5-video-player-fullscreen="true"
    oncontextmenu="return false;" 
    >
    <source :src="props.src" :type="'video/'+movie.extension"  />
    </video> 
</template>