import { ethers,BigNumber } from "ethers";
import eventBus from '@/libraries/eventBus.js';
import axios from "axios";

const state = {
  analysis: {
    heroValue: 0,
    heroBurnd: 0,
    heroCount: 0,
    heroMelee: 0,
    heroRemote: 0,
    heroCured: 0,
    treasureCount: 0,
    treasureSold: 0,
    treasureAirdrop: 0,
    goldIssued: 0,
    goldBurned: 0,
    goldSupply: 100000000,
    startTime: 0,
    normalPrice:0.1,
    whitelistPrice:0.05,
    maxLevel: 0,
    changeNameFee: 0,
    resetAttributeFee: 0,
    tradeHeroRatioFee: 0,
    redeemHeroRatioFee: 0,
    destroyHeroRatioFee:0,
    swapTokenUSDPrice:0.2,
    treasureBoxCount:0,
    treasureAirdropCount:0,
    treasureCombineCount:0,
    treasureReferrerCount:0,
    treasureWhitelistCount:0,
    referrerTotalCount:0,
    referrerTotalReward:0,
  },
  apiHost:"https://api.hashland.app",
   //apiHost:"http://api.hashland.one", // ,http://localhost:20000
  // heroHost:"http://render.hashland.one/heroes/"   //"http://render.hashland.one"
  //apiHost:"/api/",
  heroHost:"/heroes/"
};

const getters = {
  getAnalysis(state) {
    return state.analysis;
  }
};

const actions = {
  async initGame({commit,rootState}){
    let url = state.apiHost+"/game/analysis";
    axios.get(url)
    .then(r => {
      if(r.data && r.data.data &&  r.data.data.analysis){
        commit("setTime", r.data.data.timestamp);
        commit("setAnalysis", r.data.data.analysis);
      }
    })
    .catch(e => {
        eventBus.trigger("onRequestError",e);
    })
    .finally(() => {
        
    });
  }
};

const mutations = {
    setTime(state,timestamp){
        state.timestamp = timestamp;
    },
    setAnalysis(state,analysis){
        state.analysis = analysis;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
