import { ethers,BigNumber } from "ethers";
import treasureAbi from "@/contracts/treasure.json";
import eventBus from '@/libraries/eventBus.js';

const state = {
  treasureAbi: treasureAbi,
  treasureAddress: "0x936b3bD992A0467113518294a1a3cf7455ce1FE0",
  treasureSigner:null,
  treasureContract:null,
  treasureProvider:null,

  referrerId:0,
  referrerCount:0,
  referrerTotal:0,
  referrerReward:0,

  count:1,
  buying:false,
  opening:false,
  claiming:false,
  myBoxListing:false,
  createReferrerStatus:false,
  rewardReferrerBoxStatus:false, 
  rewardReferrerTokenStatus:false,
  myBoxes:[],
};

const getters = {
  getTreasureAbi(state) {
    return state.treasureAbi;
  },
  getTreasureAddress(state) {
    return state.treasureAddress;
  }
};

const actions = {
  // async getPrice({ commit, rootState }) {
  //   let treasureProvider = new ethers.providers.Web3Provider(rootState.wallet.providerWeb3Modal);
  //   let treasureContract = new ethers.Contract(state.treasureAddress, state.treasureAbi, treasureProvider);
  //   let price = await treasureContract.getPrice();
  //   commit("setPrice", (+ethers.utils.formatEther(price)).toFixed(2));
  // },
  async initMystery({commit, rootState }){

    if(!rootState.wallet.providerWeb3Modal){
      return;
    }

    let treasureProvider = new ethers.providers.Web3Provider(rootState.wallet.providerWeb3Modal);
    let treasureSigner =  treasureProvider.getSigner(); 
    let treasureContract = new ethers.Contract(state.treasureAddress, state.treasureAbi, treasureSigner);
    treasureContract.on("OnTreasureCreated", async (id, box) => {
      let owner = await state.treasureContract.ownerOf(BigNumber.from(id).toNumber());
      if(owner.toLowerCase()==rootState.wallet.account.toLowerCase()){
        eventBus.trigger("onConfirmed");
      }
    });
    treasureContract.on("OnTreasureOpened",async (id, box) => {
      let owner = await state.treasureContract.ownerOf(BigNumber.from(id).toNumber());
      if(owner.toLowerCase()==rootState.wallet.account.toLowerCase()){
        eventBus.trigger("onMysteryOpened",{id:BigNumber.from(id).toNumber(),box});
      }
    });

    treasureContract.on("OnReferrerCreated",async (id, owner) => {
      if(owner.toLowerCase()==rootState.wallet.account.toLowerCase()){
        commit("setReferrerId", id);
        eventBus.trigger("onConfirmed");
      }
    });
   
    treasureContract.on("OnReferrerWithdraw",async (owner,way,amount) => {
      if(owner.toLowerCase()==rootState.wallet.account.toLowerCase()){
        eventBus.trigger("onConfirmed");
        await actions.getReferrerInfo({commit, rootState });
      }
    });
    commit("setTreasureSigner", treasureSigner);
    commit("setTreasureContract", treasureContract);
    commit("setTreasureProvider", treasureProvider);

    
  },
  async buyMysteryBox({commit, rootState },{ signature }) {
    commit("setBuying", true);
    try{
      let rereffer = localStorage.getItem("Hashland_Referrer_Id");
      let price = (!signature || signature.length==0?rootState.game.analysis.normalPrice:rootState.game.analysis.whiteListPrice)*state.count;
      let amount = ethers.utils.parseUnits(price.toString(), 'ether');
      let tx = await state.treasureContract.buyMysteryBox(state.count,(!rereffer || rereffer.length==0?0:parseInt(rereffer)),(!signature || signature.length==0?[]:signature),{ from: rootState.wallet.account, value: amount });
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
    commit("setBuying", false);
  },

  async claimMysteryBox({commit,rootState},{ count,signature }){
    commit("setClaiming", true);
    try{
      let tx = await state.treasureContract.claimMysteryBox(count,signature,{ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
    commit("setClaiming", false);
  },

  async openMysteryBox({commit,rootState},{ id }){
    commit("setOpening", true);
    try{
      let tx = await state.treasureContract.openMysteryBox(id,{ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
    commit("setOpening", false);
  },

  async createReferrer({commit,rootState}){
    commit("setCreateReferrerStatus", true);
    try{
      let tx = await state.treasureContract.createReferrer({ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
    commit("setCreateReferrerStatus", false);
  },

  async rewardReferrerBox({commit,rootState}){
    commit("setRewardReferrerBoxStatus", true);
    try{
      let tx = await state.treasureContract.rewardReferrerBox({ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
    commit("setRewardReferrerBoxStatus", false);
  },

  async rewardReferrerToken({commit,rootState}){
    commit("setRewardReferrerTokenStatus", true);
    try{
      let tx = await state.treasureContract.rewardReferrerToken({ from: rootState.wallet.account});
      eventBus.trigger("onExecuteSuccess",tx);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
    commit("setRewardReferrerTokenStatus", false);
  },
  
  async getReferrerInfo({commit,rootState}){
    try{
      let data = await state.treasureContract.getReferrerInfo({ from: rootState.wallet.account});
      commit("setReferrer", data);
    }catch(e){
      eventBus.trigger("onExecuteError",e);
    }
    
  },

  async getMyBoxes({commit,rootState}){
    commit("setMyBoxes", []);
    commit("setLoading", true);
    try{
      let balance = await state.treasureContract.balanceOf(rootState.wallet.account);
      const multicallContract = new ethers.Contract(rootState.wallet.isMainnet==true?rootState.wallet.mainnetMulticallAddress:rootState.wallet.testnetMulticallAddress, rootState.wallet.multicallAbi, state.treasureSigner);
      const multicallArgs = [];
      for(let i=0;i<balance;i++)
      {
        const approveInterface = new ethers.utils.Interface(["function tokenOfOwnerByIndex(address owner, uint256 index)"]);
        const approveData = approveInterface.encodeFunctionData("tokenOfOwnerByIndex", [
          rootState.wallet.account,
          i,
        ]);
        multicallArgs.push({
          target: state.treasureAddress,
          callData: approveData,
        });
      }

      let data = await multicallContract.callStatic.aggregate(multicallArgs);
      for(let j=data.returnData.length-1;j>=0;j--)
      {
        let boxId = BigNumber.from(data.returnData[j]).toNumber();
        let box = await state.treasureContract.getBoxInfo(boxId);
        commit("addMyBoxes", { boxId: boxId , box: box});
      }
    }catch(e){
      commit("setMyBoxes", []);
      eventBus.trigger("onExecuteError",e);
    }
    commit("setLoading", false);
  }
};

const mutations = {
  setCount(state, count){
    state.count = count;
  },
  setPrice(state, price) {
    state.price = price;
  },
  setBuying(state,buying){
    state.buying = buying;
  },
  setOpening(state,opening){
    state.opening = opening;
  },
  setClaiming(state,claiming){
    state.claiming = claiming;
  },
  setLoading(state,listing){
    state.myBoxListing = listing;
  },
  setCreateReferrerStatus(state,status){
    state.createReferrerStatus = status;
  },
  setRewardReferrerBoxStatus(state,status){
    state.rewardReferrerBoxStatus = status;
  },
  setRewardReferrerTokenStatus(state,status){
    state.rewardReferrerTokenStatus = status;
  },
  setReferrerId(state,id){
    state.referrerId = id;
  },
  setReferrer(state,referrer){
    state.referrerId = referrer.id;
    state.referrerCount = referrer.count;
    state.referrerTotal = referrer.total;
    state.referrerReward = parseFloat(ethers.utils.formatEther(referrer.reward)).toFixed(4);
  },
  setMyBoxes(state,boxes){
    state.myBoxes = boxes;
  },
  addMyBoxes(state,box){
    state.myBoxes.push(box);
  },
  setMyBox(state,item){
    for(let i=0;i<state.myBoxes.length;i++)
    {
      if(state.myBoxes[i].boxId == item.id){
        state.myBoxes[i].box = item.box;
        return;
      }
    }
  },
  setTreasureSigner(state, treasureSigner){
    state.treasureSigner = treasureSigner;
  },
  setTreasureProvider(state, treasureProvider){
    state.treasureProvider = treasureProvider;
  },
  setTreasureContract(state, treasureContract){
    state.treasureContract = treasureContract;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
