<script setup>
  import { onMounted, onUnmounted,reactive, ref, toRefs } from 'vue';
  import { useLocale,useLocaler } from 'vue-localer';
  import { getCurrentLang } from '@/core/localer.js';
  import { mapState, mapGetters, mapMutations, mapActions } from '@/core/store.js'
  import eventBus from '@/libraries/eventBus.js';
  import { popup,toast,confirm } from '@/libraries/dialog.js'
  import { BigNumber, ethers } from "ethers";

  const { setConfirms } = mapMutations('wallet');
  const { confirms,isMainnet,mainnet,testnet } = mapState('wallet');
  const { getBalanceEth, isUserConnected } = mapGetters('wallet');
  const { addNetwork,connectWallet,disconnectWallet } = mapActions('wallet');

  const { initHero } = mapActions('hero');
  const { initGame } = mapActions('game');
  const { initMystery } = mapActions('mystery');

  const switcher = useLocaler();
  const language = useLocale();

  eventBus.on("onConnectError",function(e){
    popup("error",language.value.wallet.connectError);
  });

  eventBus.on("onExecuteError",function(e){
    e = JSON.parse(JSON.stringify(e));
    if(e && e.message)
    {
      popup("error",(e.data && e.data.message && e.data.message.length>0?""+e.data.message.replace("execution reverted: ","")+"":e.message));
    }else if(e && e.error)
    {
      popup("error",(e.error.message.length>0?""+e.error.message.replace("execution reverted: ","")+"":e.reason));
    }else{
      popup("error",language.value.wallet.executeError);
    }
  });

  eventBus.on("onExecuteSuccess",function(e){
    if(e && e.hash)
    {
      let url = isMainnet.value==true?mainnet.value[0].blockExplorerUrls[0]:testnet.value[0].blockExplorerUrls[0];
      setConfirms(1);
      let footer='<a href="'+url+"/tx/"+e.hash+'" target="_blank">'+ language.value.wallet.blockScanText +'</a>';
      popup("success",language.value.wallet.executeSuccess,footer);
    }else{
      popup("error",language.value.wallet.executeError);
    }
  });

  eventBus.on("onConnected",function(e){
    initHero();
    initMystery();
  });

  eventBus.on("onNetworkError",function(e){
    popup("error",language.value.wallet.networkError);
  });

  eventBus.on("onDisconnected",function(e){
    console.log("disconnected");
  });

  eventBus.on("onChainChanged",function(e){
    
  });
  
  eventBus.on("onConfirmed",function(e){
    setConfirms(-1);
    toast("success",language.value.wallet.transactionConfirmed);
  });
  
  eventBus.on("onApproved",function(e){
    let amount = ethers.utils.formatEther(e.value);
    setConfirms(-1);
    popup("info",language.value.wallet.approveConfirmed.replace("{num}",amount+" "+language.value.wallet.gameTokenSymbol));
  });

  eventBus.on("onRequestError",function(e){
     popup("error",e);
  });

  // 导航栏
  const navbar = reactive({
    isFixed:false,
    goToTop : ()=>{
      window.scrollTo(0,0);
    },
    switchLanguage : ()=> {
      let lang = getCurrentLang();
      if(!lang || lang=="en-US")
      {
        switcher.dispatch('setLanguage', "zh-CN");
      }else{
        switcher.dispatch('setLanguage', "en-US");
      }
    },
    changeLanguage : (lang) =>{
      switcher.dispatch('setLanguage', lang);
    },
    onScroll: ()=> {
      if(window.pageYOffset<50){
          navbar.isFixed = false;
      }else{
          navbar.isFixed = true;
      }
    },
    listen: ()=> {
      window.addEventListener('scroll', navbar.onScroll);
    },
    remove: ()=> {
      window.removeEventListener("scroll", navbar.onScroll);
    }
  });

  onMounted(() => {
    initGame();
    navbar.listen();
  });

  onUnmounted(()=>{
    navbar.remove();
  });
</script>

<template>
<div class="navbar z-30 py-4 fixed w-full m-auto" :class="{ navfixed: navbar.isFixed }">
    <div class="max-w-screen-xl mx-auto px-2 ">
      <div class="relative flex items-center justify-between min-h-16  flex-wrap">
        <div class="flex flex-1 <md:flex-wrap items-center justify-center md:items-stretch md:justify-start">
          <div class="flex-shrink-0 flex items-center">
            <img class="lg:block h-3rem w-auto animate__animated animate__fadeInLeftBig" src="/images/hashland.png" alt="Hashland">
          </div>                                                        
          <div class="w-full animate__animated animate__fadeInRightBig">
            <div class=" text-sm text-center">
              <ul class="flex md:justify-end <md:justify-center py-1.5rem sm:py-1rem link">
                <li class="px-3 py-1rem sm:px-3 lg:px-4"><router-link @click="navbar.goToTop" :to="{ name: 'home' }" :class="$route.name=='name'?'text-secondary':'text-white'" class=" hover:text-secondary rounded-md">{{language.header.home}}</router-link></li>
                <li class="px-3 py-1rem sm:px-3 lg:px-4"><router-link @click="navbar.goToTop" :to="{ name: 'mystery' }" :class="$route.name=='mystery'?'text-secondary':'text-white'" class=" hover:text-secondary rounded-md">{{language.header.mystery}}</router-link></li>
                <li class="px-3 py-1rem sm:px-3 lg:px-4"><router-link @click="navbar.goToTop" :to="{ name: 'hero' }" :class="$route.name=='hero' || $route.name=='profile'?'text-secondary':'text-white'" class=" hover:text-secondary rounded-md">{{language.header.heroes}}</router-link></li>
                <!-- <li class="px-3 py-1rem sm:px-3 lg:px-4"><router-link @click="navbar.goToTop" :to="{ name: 'staking' }" class="text-white hover:text-secondary rounded-md">{{language.header.staking}}</router-link></li> -->
                <!-- <li class="px-3 py-1rem sm:px-3 lg:px-4"><router-link @click="navbar.goToTop" :to="{ name: 'playing' }" class="text-white hover:text-secondary rounded-md">{{language.header.playing}}</router-link></li> -->
                <li class="px-3 py-1rem sm:px-3 lg:px-4"><router-link @click="navbar.goToTop" :to="{ name: 'referral' }" :class="$route.name=='referral'?'text-secondary':'text-white'"  class=" hover:text-secondary rounded-md">{{language.header.referral}}</router-link></li>
                <li class="px-3 py-1rem <sm:hidden sm:px-3 lg:px-4"><a :href="'/documents/'+language.header.whitpaperFile+'.pdf'" target="_blank" class="text-white hover:text-secondary rounded-md">{{language.header.whitepaper}}<span></span></a></li>
              </ul>
            </div>
            

              



          </div>
          <div class="flex flex-shrink-0 relative items-center inline-block px-4  animate__animated animate__fadeInRightBig">
            <img @click="navbar.switchLanguage()" class="<sm:hidden border-0.2rem border-purple-normal rounded-full lg:block h-1.8rem w-auto cursor-pointer" :src="'/images/'+getCurrentLang()+'.png'" alt="en">
            <div class=" absolute top-0 hidden ">
              <ul class="mt-2.2rem">
                <li><img class="h-1.8rem w-auto" src="/images/en.png" alt="en"></li>
                <li><img class="lg:block h-1.8rem w-auto" src="/images/jp.png" alt="en"></li>
                <li><img class="lg:block h-1.8rem w-auto" src="/images/kr.png" alt="en"></li>
              </ul>
            </div>
            <div class="sm:hidden ">
              <ul class="flex flex-row justify-center">
                <li><img class="h-1.8rem w-auto mx-0.5rem border-0.2rem border-purple-normal rounded-full " @click="navbar.changeLanguage('en-US')"  src="/images/en-US.png" alt="en"></li>
                <li><img class="h-1.8rem w-auto mx-0.5rem border-0.2rem border-purple-normal rounded-full " @click="navbar.changeLanguage('zh-CN')"  src="/images/zh-CN.png" alt="en"></li>
              </ul>
            </div>
          </div>
          
          <div class="<sm:hidden flex flex-shrink-0 items-center justify-center inline-block pl-2  animate__animated animate__fadeInRightBig">
            <div @click="disconnectWallet" v-if="isUserConnected" class="border w-11rem border-gray-400 cursor-pointer text-white hover:border-gray-100 hover:bg-gray-400 hover:bg-opacity-20 active:border-gray-100 inline-block text-sm rounded-full px-5 py-2 text-center">
              <!-- <div>{{getAccount}}</div> -->
              <div class="flex flex-row items-center justify-center">
                <svg v-if="confirms>0" class="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                  <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
                  <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                </svg>
                <span>{{getBalanceEth}} {{language.wallet.chainTokenSymbol}}</span>
              </div>
              
              
            </div>
            <div @click="connectWallet" v-if="!isUserConnected" class="border w-11rem border-gray-400 cursor-pointer text-white hover:border-gray-100 hover:bg-gray-400 hover:bg-opacity-20 active:border-gray-100 inline-block text-sm rounded-full px-5 py-2 text-center">
              {{language.header.wallet}}
            </div>
          </div>
        </div>
      </div>
    </div>
</div>
</template>


<style>
.navfixed {
  background-color: #0e002ccc;
}
.navbar .link a {
  -webkit-transition: all ease-in-out 0.3s;
  transition: all ease-in-out 0.3s;
  position: relative;
  display: block;
}
.navbar .link a:after {
  content: "";
  position: absolute;
  display: block;
  width: 0rem;
  height: 0.1rem;
  margin: auto;
  margin-top: 0.5rem;
  background: transparent;
  transition: width .3s ease, background-color .3s ease;
}
.navbar .link a:hover:after, .navbar .link.active > a:after {
  --tw-bg-opacity: 1;
  background-color: rgba(13, 169, 191, var(--tw-bg-opacity));
  width: 100%;
}
</style>
