

import { router } from '@/core/router.js';
import { localer } from '@/core/localer.js';
import eventBus from '@/libraries/eventBus.js';
import Vuex from 'vuex';
import { createApp } from 'vue'

import wallet from "@/stores/wallet.js";
import mystery from "@/stores/mystery.js";
import hero from "@/stores/hero.js";
import game from "@/stores/game.js";

// 动画样式库
import "animate.css"

// CSS样式库
import 'virtual:windi.css'

// 滚动时动画
import WOW from 'wow.js'
const wow = new WOW({
  boxClass: 'wow',
  animateClass: 'animate__animated',
  offset: 0,
  live: true
});
wow.init();

// 平滑滚动条
import VueSmoothScroll from 'vue3-smooth-scroll'

// 顶部进度条
import NProgress from 'nprogress'
NProgress.inc(0.2);
NProgress.configure({ easing: 'ease', speed: 500, showSpinner: true });
router.beforeEach((to, from, next) => {
    NProgress.start();
    next();
});
router.afterEach(() => {
    NProgress.done();
});

// 页面图表组件
import ECharts from 'vue-echarts'
import { use } from "echarts/core";
import {
    CanvasRenderer
  } from 'echarts/renderers'
  import {
    PieChart,
    RadarChart,
  } from 'echarts/charts'
  import {
    GridComponent,
    TooltipComponent,
    ToolboxComponent
  } from 'echarts/components'
  
  use([
    CanvasRenderer,
    PieChart,
    RadarChart,
    GridComponent,
    TooltipComponent,
    ToolboxComponent
  ]);

  // router.beforeEach((to, from, next) => {
  //   if (to.meta.title) {
  //     document.title = to.meta.title
  //   }
  //   next()
  // });

// 启动中间件
import App from './App.vue'
const app = createApp(App);
app.config.globalProperties.$eventBus = eventBus;
app.use(new Vuex.Store({
  modules: {
    wallet,
    mystery,
    hero,
    game
  }
}));
app.use(router);
app.use(localer);
app.use(VueSmoothScroll,{
  duration: 1000,
  updateHistory: false
});
app.component('v-chart', ECharts);
app.mount('#app')
